body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'NimbusSanRegular', sans-serif !important;
}

.no-underline {
  text-decoration: none;
}

.image-view {
  width: 100%;
  height: 50%;
}

.btn-responsive {
  width: auto;
}

.multi-line {
  white-space: pre-wrap;
  /* Preserves new lines, allows wrapping */
  word-wrap: break-word;
  /* Ensures long words break */
  overflow-wrap: break-word;
  /* Alternative to break long words */
  max-width: 100%;
}

/* Link transition Begins */

.link-text {
  text-decoration: none!important;
  background-image: linear-gradient(black, black);
  background-size: 0% 1px;
  background-position: 0 100%;
  background-repeat: no-repeat;
  transition: background-size 0.3s ease-in-out;
  color: #121314 !important;
}

.card-hover:hover .link-text:hover {
  background-size: 100% 1px;
}

.fade-effect {
  transition: all 0.3s ease-in-out;
  /* box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); */
}

.fade-effect:hover {
  transform: scale(1.01);
  /* box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2); */
}

.fit-content{
  width: fit-content!important;
}

a {
  text-underline-offset: 3px!important; /* Moves the underline 1px away from the text */
}

/* Animations */

@keyframes appear {
  from {
      opacity: 0;
      clip-path: inset(100% 0% 0% 0%);
  }
  to {
      opacity: 1;
      clip-path: inset(0% 0% 0% 0%);
  }
}

.wipe-up {
  animation: appear linear;
  animation-timeline: view();
  animation-range: entry 0% cover 40%;
}

@keyframes appeartext {
  from {
      opacity: 0;
      clip-path: inset(100% 0% 0% 0%);
  }
  to {
      opacity: 1;
      clip-path: inset(0% 0% 0% 0%);
  }
}

.wipe-up-text {
  opacity: 0;
  animation: appeartext 1s ease-out forwards;
}

@keyframes slideup {
  from {
    opacity: 0;
    clip-path: inset(100% 100% 0 0);
  }
  to {
    opacity: 1;
    clip-path: inset(0 0 0 0);
  }
}

.slide-up{
  animation: slideup linear;
  animation-timeline: view();
  animation-range: entry 0% cover 40%;
  transition: transform 1s ease-out, opacity 1s ease-out;
}

.slide-up-page {
  opacity: 0;
  transform: translateY(50px);
  animation: slideUpPage 1s ease-out forwards;
}

/* Slide-up animation */
@keyframes slideUpPage {
  from {
      opacity: 0;
      transform: translateY(50px);
  }
  to {
      opacity: 1;
      transform: translateY(0);
  }
}

@keyframes blurReveal {
  from {
    filter: blur(10px);
    opacity: 0;
  }
  to {
    filter: blur(0);
    opacity: 1;
  }
}

.text-blur-reveal {
  opacity: 0;
  animation: blurReveal 1.5s ease-in-out forwards;
}
/* Animation Ends */


@media only screen and (max-width: 768px) {
  .btn-responsive {
    width: 100%;
  }

  .p-48 {
    padding: 0 16px !important;
  }

  .fsr-21 {
    font-size: 16px !important;
  }

  .fsr-51 {
    font-size: 38px !important;
  }

  .fsr-67 {
    font-size: 51px !important;
  }

  .fsb-120 {
    font-size: 67px !important;
  }

}